import {GoogleLogin} from 'react-google-login';

const clientId = "1061492818816-amcl919qkq9llo6dk4lb2q7ro9ele81v.apps.googleusercontent.com";

function GoogleSignin(props) {

    function onSuccess(res){
        props.signin(true, res.profileObj);
        fetch('http://localhost:8080/user', {
            method: 'POST',
            headers: {
                "Content-Type": "application/json"
              },
            body: JSON.stringify(res.profileObj)
          })
          .catch(error => {
            console.log('something went wrong when sending user info: ',error);
          });
    }
    function onFailure(res){
        props.signin(false);
    }

    return (
        <div id="signInBtn">
            <GoogleLogin
                clientId={clientId}
                buttonText='Login'
                onSuccess={onSuccess}
                onFailure={onFailure}
                cookiePolicy={'single_host_origin'}
                isSignedIn={true}
                prompt='consent'
                render={(renderProps) => (
                    <button
                    onClick={renderProps.onClick}
                    disabled={renderProps.disabled}
                    className="h-4/5 bg-white shadow-lg flex flex-row items-center p-2 w-full h-full rounded-full"
                    >
                        <div className='rounded-full w-12 mr-2 flex flex-row'>
                            <img src="data:image/png;base64," alt="Google Logo" className='rounded-full w-12 p-1'/>
                        </div>
                        <h3>Log in</h3>
                    </button>
                )
                }
            />
        </div>
    )
}

export default GoogleSignin;